.info {
    position: relative;
    height: calc(100vh - 80px);
    text-align: center;
    overflow: hidden;
}

.content {
    position: relative;
    z-index: 1;
    color: #fff;
    max-width: 600px;
    margin: 10rem auto;
    padding: 20px;
}

.content h2 {
    font-size: 3em;
    margin-bottom: 10px;
}

.content h3 {
    font-size: 2em;
    margin-top: 20px;
}

.content h5 {
    font-size: 1.1em;
    margin-top: 5rem;
}

.content p {
    font-size: 1.1em;
    margin: 20px 0;
    font-family: "MainFont", sans-serif;
}

.content img {
    width: 100%;
    max-width: 300px;
    margin: 20px auto;
    display: block;
    border-radius: 8px;
}
